/* Estilos header para que la capa se posicione bien */
header.position-relative {
  position: relative;
  height: 300px;
  width: 100%;
}

/* Texto del header */
.texto-header {
  position: relative;
  background: rgba(141, 141, 141, 0.001);
  backdrop-filter: blur(5px);
  /* -webkit-backdrop-filter: blur(10px); */
  border-radius: 12px;
  padding: 1.5rem 2rem;
  color: white;
}

/* Títulos header */
.texto-xxl {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.fw-normal {
  font-weight: normal;
  font-size: 1rem;
  margin-top: 1rem;
}

/* Para que la app-wrapper tenga relative y controle z-index */
.app-wrapper.position-relative {
  position: relative;
  z-index: 1;
}

.app-wrapper {
  background-color: #000;
  position: relative;
  min-height: 100vh;
  overflow: hidden;
}






.background-gradient {
  position: fixed;
  top: 0;
  left: 0;
  width: 30%;
  height: 30%;
  z-index: -1;
  pointer-events: none;
  background: 
              radial-gradient(circle at 93% 30%, #888888d8, transparent 32%), /* Luz arriba a la derecha */
              radial-gradient(circle at 0% 5%, #7c7c7c, transparent 20%),/* Luz arriba a la izquierda */
              radial-gradient(circle at 100% 130%, #ffffff, transparent 35%), /* Luz abajo a la derecha */
              radial-gradient(circle at 7% 70%, #929292, transparent 30%); /* Luz abajo a la izquierda */
              /* radial-gradient(circle at 0% 30%, #000000, transparent 30%); */
  animation: moveGradient 4s infinite alternate ease-in-out;
  background-size: 200% 400%;
  /*filter: blur(80px);  Si querés efecto de profundidad */
}

@keyframes moveGradient {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 100% 100%;
  }
}




